import React, { useState } from "react";
import { Button, Input, Icon, Container, Grid } from "semantic-ui-react";
import "../App.css";

function Search({ searchVideo }) {
  const [searchInput, setSearchInput] = useState("");
  const [isKaraoke, setIsKaraoke] = useState(true);
  // TODO:  i18n, tailwind?

  return (
    <Grid columns="equal">
      <Grid.Column width={8}>
        <Input
          fluid
          placeholder="Search..."
          value={searchInput}
          onChange={(e) => setSearchInput(e.target.value)}
        />
      </Grid.Column>
      <Grid.Column>
        <Button
          icon
          labelPosition="left"
          onClick={() => searchVideo(searchInput)}
        >
          <Icon name="search" />
          Search
        </Button>
      </Grid.Column>
      <Grid.Column>
        <div className="items">
          <input
            type="checkbox"
            id="is-karaoke"
            checked={isKaraoke}
            value={isKaraoke}
            onChange={(e) => setIsKaraoke(e.currentTarget.checked)}
          />
          <label htmlFor="is-karaoke">Add "karaoke" to search</label>
        </div>
      </Grid.Column>
    </Grid>
  );
}

export default Search;
