import React from "react";
import { Card, Feed } from "semantic-ui-react";
import VideoItem from "./VideoItem";

function VideoQueue({ queue }) {
  return (
    <div>
      <Card fluid>
        <Card.Content>
          <Card.Header>Queue</Card.Header>
        </Card.Content>
        <Card.Content fluid>
          {queue?.length && (
            <Feed>
              {queue.map((x) => (
                <VideoItem key={x.id.videoId} item={x} />
              ))}
            </Feed>
          )}
          {!queue?.length && <h4>No item queue</h4>}
        </Card.Content>
      </Card>
    </div>
  );
}

export default VideoQueue;
