import React from "react";
import { Card, Feed } from "semantic-ui-react";

function VideoItem({ item, addToQueue, showButton }) {
  return (
    <Feed.Event>
      {/* <h3>{item.snippet.title}</h3>
              {showButton && (
                <button onClick={() => addToQueue(item)}>Add to queue</button>
              )} */}
      <Feed.Label image={item.snippet.thumbnails?.default?.url} />
      <Feed.Content>
        <Feed.Date content={item.snippet.publishedAt} />
        <Feed.Summary>{item.snippet.title}</Feed.Summary>
        {showButton && (
          <button onClick={() => addToQueue(item)}>Add to queue</button>
        )}
      </Feed.Content>
    </Feed.Event>
  );
}

export default VideoItem;
