import Search from "./components/Search";
import SearchResult from "./components/SearchResult";
import VideoPlayer from "./components/VideoPlayer";
import VideoQueue from "./components/VideoQueue";
import { useState } from "react";
import axios from "axios";
import { Container, Grid } from "semantic-ui-react";
import React from "react";
import { Helmet } from "react-helmet";

const TITLE = "YTQueue | Play video in queue, great for Karaoke nights";

function App() {
  const [searchResults, setSearchResults] = useState([]);
  const [videoToPlay, setVideoToPlay] = useState({});
  const [queue, setQueue] = useState([]);

  const ytqueueServer = process.env.REACT_APP_YTQUEUE_SERVER || "";

  const searchVideo = async (searchQuery) => {
    const { data } = await axios.get(
      `${ytqueueServer}api/search/${searchQuery}`
    );
    setSearchResults(data);
  };

  const addToQueue = (item) => {
    if (queue.length < 1) {
      setVideoToPlay(item);
    }
    item.isPlayed = false;
    setQueue([...queue, item]);
  };

  const playNextInQueue = (etag) => {
    const playedItemIndex = queue.findIndex((x) => x.etag === etag);
    console.log(
      "ðŸš€ ~ file: App.js ~ line 34 ~ playNextInQueue ~ playedItemIndex",
      playedItemIndex
    );

    if (playedItemIndex >= 0 && playedItemIndex !== queue.length) {
      const nextItemToPlay = queue[playedItemIndex + 1];
      setVideoToPlay(nextItemToPlay);
    }
  };

  return (
    <Container>
      <Helmet>
        <title>{TITLE}</title>
      </Helmet>
      <Search searchVideo={searchVideo} />
      <Grid columns="equal">
        <Grid.Column>
          {searchResults?.length > 0 && (
            <SearchResult
              searchResults={searchResults}
              addToQueue={addToQueue}
            />
          )}
          <VideoPlayer videoToPlay={videoToPlay} onEnded={playNextInQueue} />
        </Grid.Column>
        <Grid.Column>
          {/* maintain state of queue */}
          <VideoQueue queue={queue} />
        </Grid.Column>
      </Grid>
    </Container>
  );
}

export default App;
